
@include font('Nautilus Pompilius', NautilusPompilius);
@mixin NP {
	font-family: "Nautilus Pompilius", sans-serif; }

@include font('Helvetica Neue LT Std', HelveticaNeueLTStd);
@mixin HN {
	font-family: "Helvetica Neue LT Std", sans-serif; }

@include font('Helvetica Neue Cyr Black', HelveticaNeueCyr-Black);
@mixin HNc {
	font-family: "Helvetica Neue Cyr Black", sans-serif; }

@include font('Museo Sans Cyrl', MuseoSansCyrl);
@mixin MSC {
	font-family: "Museo Sans Cyrl", sans-serif; }

@include font('FreeSet', Freeset);
@mixin Fs {
	font-family: "FreeSet", sans-serif; }

@include font('Kizo', Kizo);
@mixin K {
	font-family: "Kizo", sans-serif; }

body {
  background-color: #131313;
  @include css-lock(16, 26, 480, 1366);
  line-height: 1.4; }

.wrapper {
	max-width: 1366px;
	margin: 0 auto; }
.content-wrapper {
	max-width: 954px;
	margin: 0 auto; }

@mixin cw {
	max-width: 954px;
	margin: 0 auto; }

@mixin love-text {
	background-image: linear-gradient(39deg, #fd586e 40%, #f9579e 60%);
	-o-background-clip: text;
	background-clip: text;
	-webkit-background-clip: text;
	// color: #ffffff
	color: transparent;
	@include NP; }
